import React from "react";
import { Helmet } from "react-helmet-async";
import Hero from "../components/Hero";
import Thumbnail from "../components/Thumbnail";
import ShimmerThumb from "../components/ShimmerThumbnail";
import CONSTANTS from "../assets/constants.json";

function Home({ posts }) {
  const randomIndex = Math.floor(
    Math.random() * (CONSTANTS.HERO_IMAGES.length - 1)
  );
  return (
    <div className="m-0 p-0 w-full">
      <Helmet>
        <title>My Journey!</title>
        <meta
          name="description"
          content="Read our latest blog posts about React, programming, and more."
        />
      </Helmet>
      <Hero image={CONSTANTS.HERO_IMAGES[randomIndex]} />

      <div
        className="grid grid-cols-1 p-2 md:px-24 md:py-12 bg-white border-blue-600/[0.2]"
        id="postsContainer"
      >
        {posts.length === 0 ? (
          <div>
            {[1, 2, 3].map((i) => (
              <ShimmerThumb index={i} key={i} />
            ))}
          </div>
        ) : (
          posts &&
          posts.map((post) => (
            <div key={post.id} className="p-4 rounded-lg">
              <Thumbnail post={post} />
            </div>
          ))
        )}
      </div>
    </div>
  );
}

export default Home;
