import React from "react";

function HtmlRender({ html, excerptLength, isExcerpt }) {
  let content = isExcerpt ? html.substring(0, excerptLength) + " ..." : html;

  

  return (
    <div
      dangerouslySetInnerHTML={{
        __html: content,
      }}
    ></div>
  );
}

export default HtmlRender;
